@charset 'utf-8';

.tui-pagination * {
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
    overflow: hidden;
}

.tui-pagination {
    height: 28px;
    margin: 14px 0 12px;
    line-height: normal;
    text-align: center;
    font-size: 0;
}

.tui-pagination .tui-page-btn {
    display: inline-block;
    position: relative;
    width: 28px;
    padding: 8px 0 6px;
    margin-left: 0;
    color: #333;
    font-size: 12px;
    font-weight: normal;
    line-height: 1;
    text-decoration: none;
    vertical-align: middle;
    border: 1px solid #ddd;
    border-width: 1px 0;
}

.tui-pagination .tui-page-btn:hover {
    background-color: #f4f4f4;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
    color: #fff;
    background: #4b96e6;
    border-color: #4b96e6;
    cursor: default;
}

.tui-pagination .tui-is-selected:hover {
    background-color: #4b96e6;
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
    margin-left: 0
}

.tui-pagination .tui-first-child,
.tui-pagination .tui-prev-is-ellip {
    border-left: 1px solid #ddd
}

.tui-pagination .tui-last-child,
.tui-pagination .tui-next-is-ellip {
    border-right: 1px solid #ddd
}

.tui-pagination .tui-first-child.tui-is-selected {
    border-left: 1px solid #4b96e6
}

.tui-pagination .tui-last-child.tui-is-selected {
    border-right: 1px solid #4b96e6
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
    overflow: hidden;
    height: 26px;
    padding: 0;
    font-size: 0;
    line-height: 26px;
    white-space: nowrap;
    font-weight: normal;
    border: 1px solid #ddd;
}

.tui-pagination .tui-prev-is-ellip {
    border-right: 0
}

.tui-pagination .tui-next-is-ellip {
    border-left: 0
}

.tui-pagination .tui-first+.tui-prev,
.tui-pagination .tui-next+.tui-last {
    border-left: 0
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
    background: url(data:image/png;base64,) no-repeat 0 0;
    display: inline-block;
    vertical-align: middle;
    font-size: 0px;
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
    padding: 0;
    line-height: 1;
}

.tui-pagination .tui-prev {
    margin-right: 0px;
}

.tui-pagination .tui-next {
    margin-left: 0px;
}

.tui-pagination .tui-ico-first {
    width: 11px;
    height: 10px;
    background-position: 0px 0px
}

.tui-pagination .tui-ico-prev {
    width: 6px;
    height: 10px;
    background-position: -26px 0px
}

.tui-pagination .tui-ico-next {
    width: 6px;
    height: 10px;
    background-position: -13px -24px
}

.tui-pagination .tui-ico-last {
    width: 11px;
    height: 10px;
    background-position: 0px -12px
}

.tui-pagination .tui-ico-ellip {
    width: 10px;
    height: 2px;
    margin-top: 16px;
    vertical-align: top;
    background-position: 0px -24px
}

.tui-pagination .tui-is-disabled:hover {
    background: #fff;
    cursor: default
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
    background-position: -26px -12px
}

.tui-pagination .tui-is-disabled .tui-ico-first {
    background-position: -13px 0px
}

.tui-pagination .tui-is-disabled .tui-ico-next {
    background-position: -21px -24px
}

.tui-pagination .tui-is-disabled .tui-ico-last {
    background-position: -13px -12px
}

.tui-page-inputs {
    display: inline-block;
    vertical-align: top;
}

.tui-page-inputs .tui-page-input {
    display: inline-block;
    width: 10px;
    height: 12px;
    padding: 8px 0 6px;
    font-size: 12px;
    text-align: center;
    color: #333;
    border: 0;
    outline: none;
}

.tui-page-inputs .tui-page-current {
    color: #4b96e6
}

.tui-page-separator {
    display: inline-block;
    font-size: 12px;
    height: 12px;
    padding: 8px 0 6px;
    vertical-align: top
}
.tui-pagination * {
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.tui-pagination {
    display: flex;
    justify-content: center;
    height: 40px;
    margin: 14px 0 60px;
    line-height: normal;
    text-align: center;
    font-size: 0;
}

.tui-pagination .tui-page-btn {
    // cursor: pointer;
    display: flex;
    position: relative;
    padding: 16px;
    border-radius: 5px;
    justify-content: center;
    align-items: center;
    background: inherit;
    color: inherit;
    font-size: 12px;
    font-weight: 500;
    line-height: 1;
    text-decoration: none;
    vertical-align: middle;
    border: none;
    width: 40px;
    height: 40px;
    transition: all 0.5s cubic-bezier(0, 0.45, 0, 1.15);
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
    @media screen and (max-width: 425px) {
        display: none;
    }

    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 16px;
}

.tui-pagination .tui-page-btn:hover {
    background-color: #db2020;
    color: var(--base-white);
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
    height: 40px;
    width: 40px;
    color: var(--base-white);
    background: var(--acent-red);
    border-color: var(--acent-red);
    cursor: default;
}

.tui-pagination .tui-prev,
.tui-pagination .tui-next {
    height: 40px;
    width: 40px;
    background-color: #f7f7f7;
}

.tui-pagination .tui-ico-ellip::before {
    content: '';
    visibility: visible;
}

.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next {
    font-size: 0;
}

.tui-pagination .tui-first,
.tui-pagination .tui-last,
.tui-pagination .tui-is-disabled {
    display: none;
}

.tui-pagination .tui-ico-prev::before {
    content: '';
    width: 16px;
    height: 16px;
    background-image: url('../icons/arrow-left.svg');
    background-repeat: no-repeat;
    background-size: contain;
    display: flex;
    justify-content: center;
    background-size: contain;
    visibility: visible;
}

.tui-pagination .tui-ico-next::before {
    content: '';
    display: inline-flex;
    width: 16px;
    height: 16px;
    background-image: url('../icons/arrow-right.svg');
    background-repeat: no-repeat;
    background-size: contain;
    display: flex;
    justify-content: center;
    background-size: contain;
    visibility: visible;
}

.tui-pagination .tui-page-btn .tui-prev {
    visibility: hidden;
}

body.darkTheme {
    background-color: rgba(22, 20, 20, 0.94);

    .tui-pagination .tui-page-btn {
        background-color: #333;
        color: #ffff;
    }

    .tui-pagination .tui-page-btn:hover {
        background-color: #0000;
    }

    .tui-pagination .tui-is-selected,
    .tui-pagination strong {
        background: var(--acent-red);
        border-color: var(--acent-red);
    }
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
    vertical-align: middle;
   background: none;
    font-size: 0;
    display: inline-block;
}
.tui-pagination .tui-page-btn {
    padding: 0;
}
.tui-pagination .tui-ico-next{
    width: 16px;
    height: 16px;
}
.tui-pagination .tui-first-child.tui-is-selected {
    border-left: none;
}

.tui-pagination .tui-ico-prev {
    width: 16px;
    height: 16px;
}

.tui-pagination .tui-prev{
    margin: 0;
}
.tui-ico-ellip{
    position: relative;
    display: block;
}
.tui-pagination .tui-ico-ellip:before {
    content: "...";
    visibility: visible;
    color: #000;
    display: block;
}
.tui-pagination .tui-page-btn:hover{
    cursor: pointer;
}

.tui-pagination {
    width: 280px;
    height: 40px;
    gap: 6px;
    
    text-align: center;
    margin: 14px 0 60px;
    font-size: 0;
    line-height: normal;
    margin: 0 auto;
    display: flex;
    align-items: center;
    @include tablet {
            width: 374px;
            gap: 0px;
        }
}
.tui-pagination .tui-prev-is-ellip, .tui-pagination .tui-next-is-ellip {
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 16px;
    /* display: flex; */
    display: none;
}